<template>
  <div class="hs-base-component">
    <template v-if="!isMiniprogram">
      <transition :name="`vux-pop-${direction === 'forward' ? 'in' : 'out'}`">
        <keep-alive>
          <router-view class="router-view" v-if="$route.meta.keepAlive"/>
        </keep-alive>
      </transition>
      <transition :name="`vux-pop-${direction === 'forward' ? 'in' : 'out'}`">
        <router-view class="router-view" v-if="!$route.meta.keepAlive"/>
      </transition>
    </template>
    <template v-else>
      <keep-alive>
        <router-view class="router-view" v-if="$route.meta.keepAlive"/>
      </keep-alive>
      <router-view class="router-view" v-if="!$route.meta.keepAlive"/>
    </template>
    <!-- <div class="iphoneX"></div> -->
  </div>
</template>
<script type="text/ecmascript-6">
// 最简单的基础component, 只有一层router-view, 适用于所有页面
import {mapGetters, mapMutations} from 'vuex'
import {kpiWeChatOAuthWeChatJsConfigResponse} from 'api'
export default {
  mounted() {
    this._initWechatShare()
  },
  computed: {
    ...mapGetters([
      'direction',
      'isMiniprogram'
    ])
  },
  data() {
    return {
      wimg: `${window.location.protocol}//${window.location.host}/static/image/base/logo_70.png`,
      wurl: `${window.location.protocol}//${window.location.host}/#/wechatShare`,
      wdesc: '全国服务热线: 4006-123-123',
      wtit: '恒顺旅行',
      version: window.localStorage.version
    }
  },
  methods: {
    ...mapMutations([
      'SET_IS_MINIPROGRAM',
      'SET_IS_WECHAT_BROWSER'
    ]),
    _initView() {
      // eslint-disable-next-line
      if (!window.WeixinJSBridge || !WeixinJSBridge.invoke) {
        document.addEventListener('WeixinJSBridgeReady', this._ready(), false)
      } else {
        this._ready()
      }
    },
    _ready() {
      this.SET_IS_MINIPROGRAM(window.__wxjs_environment === 'miniprogram')
      let ua = window.navigator.userAgent.toLowerCase() // 获取判断用的对象
      this.SET_IS_WECHAT_BROWSER(/micromessenger/.test(ua))
    },
    _initWechatShare() {
      let params = {
        AbsoluteUri: window.location.href.split('#')[0]
      }
      kpiWeChatOAuthWeChatJsConfigResponse(params).then((res) => {
        console.log('分享： ', res)
        this._setWechatConfig(res.ResultData)
      })
    },
    _setWechatConfig(ResultData) {
      // eslint-disable-next-line
      wx.config({
        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        appId: ResultData.AppId, // 必填，公众号的唯一标识
        timestamp: ResultData.Timestamp, // 必填，生成签名的时间戳
        nonceStr: ResultData.NonceStr, // 必填，生成签名的随机串
        signature: ResultData.Signature, // 必填，签名
        jsApiList: [
          'onMenuShareTimeline',
          'onMenuShareAppMessage'
        ]
      })
      // eslint-disable-next-line
      // wx.error(function (res) {
      //   console.log(res)
      //   alert('验证失败')
      // })
      this._shareMsg()
    },
    _shareMsg() {
      let _this = this
      // eslint-disable-next-line
      wx.ready(function () { // 需在用户可能点击分享按钮前就先调用
        _this._initView()
        // eslint-disable-next-line
        wx.onMenuShareTimeline({
          title: _this.wtit, // 分享标题
          link: _this.wurl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
          imgUrl: _this.wimg // 分享图标
        }, function(res) {
          // 这里是回调函数
        })
        // eslint-disable-next-line
        wx.onMenuShareAppMessage({
          title: _this.wtit, // 分享标题
          desc: _this.wdesc, // 分享描述
          link: _this.wurl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
          imgUrl: _this.wimg // 分享图标
        }, function(res) {
          // 这里是回调函数
          console.log('分享到朋友成功')
        })
      })
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-base-component
  width 100%
  height 100%
  .router-view
    width 100%
    height 100%
    // height calc(100% - 20px)
  .iphoneX
    width 100%
    height 20px
    background-color green
</style>



